
class FrVersus {
  constructor() {
    this.mobileMoreButton = document.querySelector(`#vs-mobile-more`);
    this.desktopMoreButton = document.querySelector(`#vs-more`);

    this.mobileMoreButton.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      this.showAllMobile();
    });

    this.desktopMoreButton.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      this.showAllDesktop();
    });
  }

  showAllMobile() {
    const contentBlock = document.querySelector(`.fr-with-franchise__content`);
    contentBlock.classList.toggle(`fr-with-franchise__content--shown-mobile`);
  }

  showAllDesktop() {
    const comparsonTable = document.querySelector(`#vs-quantity`);
    comparsonTable.classList.toggle(`fr-with-franchise__comparson--closed`);
    // this.desktopMoreButton.remove();
  }
}

export default FrVersus;
