import RangeSlider from 'rangeslider-pure';


const updateSliderGroup = (slider, position, value) => {
  const parent = slider.parentNode;
  const input = parent.querySelector(`.fr-calculator__input`);
  input.value = position;
};


const calcTax = (income, taxType) => {
  let taxInMoney = 0;
  switch (taxType) {
      case "usn6":
          taxInMoney = income * 6 / 100;
          break;
      case "usn3":
          taxInMoney = income * 3 / 100;
          break;
      case "patent":
          taxInMoney = 56000 / 12;
          break;
      case "profit":
      break;
      
      default:
          taxInMoney = 0;
          break;
  }
  return parseInt(taxInMoney);
};


const calcCosmetics = (income) => {
  const COSMETICS_SHARE = 0.1;
  return parseInt(income * COSMETICS_SHARE);
};


const calcRoyalty = (income) => {
  const ROYALTY_PERCENT = 5;
  return parseInt(income * ROYALTY_PERCENT / 100);
};


class FrCalculator {
  constructor() {
    this.init();
  }


  init() {
    // Getting franchise calculator form
    this._calcForm = document.querySelector(`#fr-calculator`);
    if (!this._calcForm) {
      throw new Error(`Can't find franchise calculator from`);
    }

    // Getting calculators inputs and polyfill them
    const sliders = this._calcForm.querySelectorAll(`.fr-calculator__range`);
    sliders.forEach((slider) => {
      new RangeSlider(slider, {
        polyfill: true,
      
        // Default CSS classes
        rangeClass: 'rangeslider',
        disabledClass: 'rangeslider--disabled',
        fillClass: 'rangeslider__fill',
        bufferClass: 'rangeSlider__buffer',
        handleClass: 'rangeslider__handle',
    
        onInit: () => {
          // console.info('onInit');
        },
        onSlideStart: function (position, value) {
            // console.info('onSlideStart', 'position: ' + position, 'value: ' + value);
        },
        onSlide: (position, value) => {
          updateSliderGroup(slider, position, value);
          this.calcProfit();
        },
        onSlideEnd: (position, value) => {
          updateSliderGroup(slider, position, value);
          this.calcProfit();
        },
      });
    });

    this.initFields();

    this.calcProfit();
  }


  initFields() {
    this._rentInput           = this._calcForm.querySelector(`#fc-rent`);
    this._avgCheckInput       = this._calcForm.querySelector(`#fc-average-check`);
    this._adsCostInput        = this._calcForm.querySelector(`#fc-ads-cost`);
    this._barberShareInput    = this._calcForm.querySelector(`#fc-barber-share`);
    this._clientsNumInput     = this._calcForm.querySelector(`#fc-clients-num`);
    this._expensesInput       = this._calcForm.querySelector(`#fc-expenses`);
    this._receptionInput      = this._calcForm.querySelector(`#fc-receptionist`);
    this._taxInput            = this._calcForm.querySelector(`#fc-tax`);
    this._consumablesInput    = this._calcForm.querySelector(`#fc-consumables`);
    this._acquiringInput      = this._calcForm.querySelector(`#fc-acquiring`);
    this._cosmeticsInput      = this._calcForm.querySelector(`#fc-cosmetics`);
    this._royaltyInput        = this._calcForm.querySelector(`#fc-royalty`);

    this._taxInput.addEventListener(`change`, () => {
      this.calcProfit();
    })
  }


  calcProfit() {
      // Getting field values
      const rent            = parseInt(this._rentInput.value);
      const avgCheck        = parseInt(this._avgCheckInput.value);
      const adsCost         = parseInt(this._adsCostInput.value);
      const barberShare     = parseInt(this._barberShareInput.value);
      const clientsNum      = parseInt(this._clientsNumInput.value);
      const expenses        = parseInt(this._expensesInput.value);
      const reception       = parseInt(this._receptionInput.value);

      // Calculating profit
      const VISITS_PER_MONTH      = 1;
      const EXPENSES_PER_CLIENT   = 30;
      const ACQUIRING_PERCENT     = 0.8;

      const income = clientsNum * avgCheck * VISITS_PER_MONTH;
      const barbersSalary = income * barberShare / 100;
      const shavingCosts = EXPENSES_PER_CLIENT * clientsNum * VISITS_PER_MONTH;
      this.setConsumables(shavingCosts);

      const acquiring = income * ACQUIRING_PERCENT / 100;
      this.setAcquiring(acquiring);

      const taxInMoney = calcTax(income, this._taxInput.value);

      const cosmetics = calcCosmetics(income);
      this.setCosmetics(cosmetics);

      const royalty = calcRoyalty(income);

      const profit = income
                  - rent
                  - expenses
                  - reception
                  - adsCost
                  - shavingCosts
                  - barbersSalary
                  - taxInMoney
                  + cosmetics
                  - royalty
                  - acquiring;

      this.updateProfit(profit);
  }


  setConsumables(value) {
    this._consumablesInput.value = value;
  }


  setCosmetics(value) {
    this._cosmeticsInput.value = value;
  }


  setAcquiring(value) {
    this._acquiringInput.value = value;
  }


  updateProfit(value) {
    const profitField = this._calcForm.querySelector(`#fc-profit`);
    profitField.textContent = value + ` ₽`;
  }
};


export default FrCalculator;
